import Typography from '@material-ui/core/Typography';
import React from 'react';
import { LinearProgress, Grid } from '@material-ui/core';

export default class AreaExpansionPanelDetail extends React.Component < {area?: string, areaCode?: string} > {
    
    state = {
        ready: false
    }

    constructor(props) {

        super(props);
        this.state = {
            ready: false
        }
    }

    componentDidMount() {
        this.setState({ready: true});
    }

    render () {
        return this.state.ready ? 
        <Container>
            <Grid></Grid>
        </Container> : <LinearProgress />
    }
}